import React, { useState } from "react";
import { useParams, useHistory } from "react-router-dom";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { useSession, auth } from "../services/firebase";
import { useMutation, gql, useQuery } from "@apollo/client";
import Spinner from "../components/Spinner";
import InputSeed from "../components/InputSeed";

const ADD_SEED = gql`
  mutation AddSeed($input: AddSeedInput) {
    addSeed(input: $input) {
      id
    }
  }
`;

const UPDATE_SEED = gql`
  mutation UpdateSeed($id: ID!, $patch: UpdateSeedInput) {
    updateSeed(id: $id, patch: $patch) {
      id
    }
  }
`;

const GET_USER = gql`
  query GetUser($id: ID!) {
    user(id: $id) {
      id
      places {
        id
        name
      }
    }
  }
`;

const GET_PLACE = gql`
  query GetPlace($id: ID!) {
    place(id: $id) {
      id
      name
      seeds {
        name
        id
        date
        count
        isPublic
        waterConditions
        soilConditions
        sunConditions
      }
    }
  }
`;

const REMOVE_SEED = gql`
  mutation RemoveSeed($id: ID!) {
    removeSeed(id: $id) {
      id
    }
  }
`;

const REMOVE_PLACE = gql`
  mutation RemovePlace($id: ID!) {
    removePlace(id: $id) {
      id
    }
  }
`;

export default function MySeeds() {
  const history = useHistory();
  const userSession = useSession();
  let { placeId } = useParams();

  const [addSeedsModalIsOpen, setAddSeedsModalIsOpen] = useState(false);
  const [editSeedsModalIsOpen, setEditSeedsModalIsOpen] = useState(false);
  const [seedToEdit, setSeedToEdit] = useState(undefined);

  const { data: userData, loading: loadingUser } = useQuery(GET_USER, {
    variables: { id: userSession.uid },
  });

  const { data: placeData, loading: loadingPlace } = useQuery(GET_PLACE, {
    variables: { id: placeId },
  });

  const [updateSeed] = useMutation(UPDATE_SEED);
  const [addSeed] = useMutation(ADD_SEED);
  const [removeSeed] = useMutation(REMOVE_SEED);
  const [removePlace] = useMutation(REMOVE_PLACE);

  return (
    <>
      <Header
        user={userSession}
        onSignOut={() => {
          auth.signOut();
        }}
      ></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="md" margin-y="md">
          <Our.Button
            onClick={() => history.push("/my-listings")}
            type="transparent"
          >
            <i slot="start" className="gg-arrow-left"></i> Places
          </Our.Button>
        </Our.Box>
        <Our.Box padding-x="lg" margin-y="xl">
          {placeData && (
            <Our.Box margin-y="xl">
              <Our.Text className="main" weight="500" tag="h1">
                {placeData.place.name}
              </Our.Text>
            </Our.Box>
          )}
        </Our.Box>
        <Our.Box>
          <Our.Tabs>
            <Our.Tab>All</Our.Tab>
            <Our.Tab>RatPark</Our.Tab>
            <Our.Tab>Guatuso</Our.Tab>
          </Our.Tabs>
        </Our.Box>
        <Our.Box padding-x="lg" margin-y="lg">
          <Our.Flex align-items="center" justify-content="between">
            <Our.Text weight="500" inline tag="h2">
              Seeds
            </Our.Text>
            <Our.Button
              type="transparent"
              onClick={() => setAddSeedsModalIsOpen(true)}
            >
              <i className="gg-math-plus"></i>
            </Our.Button>
          </Our.Flex>
        </Our.Box>
      </Our.Container>
      <Our.Container center size="md">
        <Our.Box padding-y="lg">
          {loadingPlace ? (
            <Our.Flex justify-content="center" align-items="center">
              <Spinner></Spinner>
            </Our.Flex>
          ) : placeData && placeData.place.seeds.length ? (
            <div className="table-wrapper">
              <table>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Count</th>
                    <th>Public</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {placeData &&
                    placeData.place.seeds.map((seed, i) => {
                      return (
                        <TableRow
                          onEdit={() => {
                            setSeedToEdit(seed);
                            setEditSeedsModalIsOpen(true);
                          }}
                          onRemove={() => {
                            removeSeed({
                              variables: { id: seed.id },
                              optimisticResponse: {
                                __typename: "Mutation",
                                removeSeed: {
                                  id: seed.id,
                                  __typename: "RemoveSeedPayload",
                                },
                              },
                              update: (cache, { data: { removeSeed } }) => {
                                // Read the data from our cache for this query.
                                const data = cache.readQuery({
                                  query: GET_PLACE,
                                  variables: { id: placeId },
                                });
                                // Write our data back to the cache with the new comment in it
                                cache.writeQuery({
                                  query: GET_PLACE,
                                  data: {
                                    place: {
                                      ...data.place,
                                      seeds: [
                                        ...data.place.seeds.filter(
                                          (seed) => seed.id !== removeSeed.id
                                        ),
                                      ],
                                    },
                                  },
                                });
                              },
                            });
                          }}
                          seed={seed}
                          key={seed.id}
                        ></TableRow>
                      );
                    })}
                </tbody>
              </table>
            </div>
          ) : (
            <Our.Text tag="h3" weight="400" align="center">
              You have no seeds registered in this place.
            </Our.Text>
          )}
        </Our.Box>
        <Our.Box padding-y="lg" padding-x="lg">
          <Our.Button
            full
            onClick={() => {
              removePlace({
                variables: { id: placeData.place.id },
                optimisticResponse: {
                  __typename: "Mutation",
                  removePlace: {
                    id: placeData.place.id,
                    __typename: "RemovePlacePayload",
                  },
                },
                update: (cache, { data: { removePlace } }) => {
                  // Read the data from our cache for this query.
                  const data = cache.readQuery({
                    query: GET_USER,
                    variables: { id: userSession.uid },
                  });
                  // Write our data back to the cache with the new comment in it
                  cache.writeQuery({
                    query: GET_USER,
                    data: {
                      user: {
                        ...data.user,
                        places: [
                          ...data.user.places.filter(
                            (place) => place.id !== removePlace.id
                          ),
                        ],
                      },
                    },
                  });
                },
              });
              history.push("/my-listings");
            }}
          >
            <i slot="start" className="gg-trash"></i> Delete place
          </Our.Button>
        </Our.Box>
      </Our.Container>
      <Our.Modal
        title="Edit Seed"
        open={editSeedsModalIsOpen}
        onToggle={(e) => setEditSeedsModalIsOpen(e.target.open)}
      >
        <InputSeed
          initialState={seedToEdit}
          places={userData && userData.user.places}
          onSubmit={(input) => {
            setEditSeedsModalIsOpen(false);
            updateSeed({
              variables: { id: seedToEdit.id, patch: input },
              refetchQueries: [
                {
                  query: GET_PLACE,
                  variables: { id: placeId },
                },
              ],
            });
          }}
        ></InputSeed>
      </Our.Modal>
      <Our.Modal
        title="Add Seed"
        open={addSeedsModalIsOpen}
        onToggle={(e) => setAddSeedsModalIsOpen(e.target.open)}
      >
        <InputSeed
          resetOnSubmit
          places={userData && userData.user.places}
          initialState={{
            name: "",
            count: 10,
            isPublic: false,
            date: "",
            placeId: placeId,
            sunConditions: [],
            waterConditions: [],
            soilConditions: [],
            temperatureRange: { start: 10, end: 30 },
          }}
          onSubmit={(input) => {
            setAddSeedsModalIsOpen(false);
            addSeed({
              variables: { input: { ...input, userId: userSession.uid } },
              refetchQueries: [
                {
                  query: GET_PLACE,
                  variables: { id: placeId },
                },
              ],
            });
          }}
        ></InputSeed>
      </Our.Modal>
    </>
  );
}

function TableRow({ seed, onRemove, onEdit }) {
  return (
    <tr>
      <td>{seed.name}</td>
      <td>{seed.count}</td>
      <td>{seed.isPublic ? "Yes" : "No"}</td>
      <td className="align-right">
        <Our.Button type="transparent" onClick={onEdit}>
          <span>Edit</span>
        </Our.Button>
        <Our.Button type="transparent" onClick={onRemove}>
          <span style={{ color: "var(--core-color-danger)" }}>Delete</span>
        </Our.Button>
      </td>
    </tr>
  );
}
