import React, { useState } from "react";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { auth, useSession } from "../services/firebase";

export default function MyAccount() {
  const userSession = useSession();
  const [darkMode, setDarkMode] = useState(document.body.hasAttribute("mode"));

  function toggleMode(e) {
    if (!e.target.checked) {
      setDarkMode(false);
      document.body.removeAttribute("mode");
    } else {
      document.body.setAttribute("mode", "dark");
      setDarkMode(true);
    }
  }

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="lg" margin-y="xl">
          <Our.Text weight="500" inline tag="h1">
            Account
          </Our.Text>
        </Our.Box>
        <Our.Box padding-x="lg" margin-y="md">
          <Our.Toggle full checked={darkMode} onChange={toggleMode}>
            {darkMode ? (
              <i className="gg-moon"></i>
            ) : (
              <i className="gg-sun"></i>
            )}
          </Our.Toggle>
        </Our.Box>
        <Our.Box padding-x="lg" margin-y="md">
          <Our.Button full onClick={() => auth.signOut()}>
            Logout
          </Our.Button>
        </Our.Box>
      </Our.Container>
    </>
  );
}
