import React, { useState } from "react";
import { useSession, auth } from "../services/firebase";

import * as Our from "../components/Core";
import Header from "../components/Header";
import MapSearchInput from "../components/MapSearchInput";
import { useHistory } from "react-router-dom";

export default function Home() {
  const user = useSession();
  const history = useHistory();

  function handleSelectPlace(place) {
    history.push(`/results?&lat=${place.lat}&lng=${place.lng}`);
  }

  function findMe() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((pos) => {
        const lat = pos.coords.latitude;
        const lng = pos.coords.longitude;
        handleSelectPlace({ lat, lng });
      });
    } else {
      console.log("Geolocation is not supported by this browser.");
    }
  }

  return (
    <div id="home">
      <Header
        user={user}
        onSignOut={() => {
          auth.signOut();
        }}
      ></Header>

      <Our.Container center size="md">
        <Our.Box padding-x="lg" margin-y="xl" padding-y="xl">
          <Our.Text
            inline
            className="main-heading"
            weight="500"
            look=""
            tag="h1"
          >
            Find seeds that grow in your climate
            <i
              style={{ fontSize: "100px", color: "var(--core-color-primary)" }}
            >
              .
            </i>
          </Our.Text>

          <Our.Container size="sm">
            <Our.Box margin-y="lg" padding="md" depth="sm">
              <Our.Grid columns="9">
                <Our.GridItem sm="9" md="4">
                  <MapSearchInput
                    size="xl"
                    placeholder="Enter your city, place or address"
                    onSelect={(place) => {
                      handleSelectPlace(place);
                    }}
                  ></MapSearchInput>
                </Our.GridItem>
                <Our.GridItem sm="9" md="1">
                  <Our.Flex
                    full-height
                    align-items="center"
                    justify-content="center"
                  >
                    or
                  </Our.Flex>
                </Our.GridItem>
                <Our.GridItem sm="9" md="4">
                  <Our.Button
                    onClick={() => findMe()}
                    full
                    size="xl"
                    type="primary"
                  >
                    <i slot="start" className="gg-pin"></i>
                    Find me
                  </Our.Button>
                </Our.GridItem>
              </Our.Grid>
            </Our.Box>
          </Our.Container>
        </Our.Box>
      </Our.Container>
    </div>
  );
}
