import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

import { ApolloProvider } from "@apollo/client";
import { client } from "./graphql/index.js";
import { useAuth, UserContext } from "./services/firebase";

import * as Our from "./components/Core";

import Home from "./screens/Home";
import Spinner from "./components/Spinner";
import Login from "./screens/Login";
import MyPlace from "./screens/MyPlace";
import MyAccount from "./screens/MyAccount";
import MyBookmarks from "./screens/MyBookmarks";
import MyListings from "./screens/MyListings";
import SearchResults from "./screens/SearchResults";

function PrivateRoute({ component: Component, isAuthenticated, ...rest }) {
  return (
    <Route
      {...rest}
      render={(props) =>
        isAuthenticated ? (
          <Component {...props} />
        ) : (
          <Redirect to={{ pathname: "/", state: { from: props.location } }} />
        )
      }
    />
  );
}

function App() {
  const { initializing, user } = useAuth();
  if (initializing) {
    return (
      <Our.Container center size="xs">
        <Our.Box padding-y="xl">
          <Our.Flex align-items="center" justify-content="center">
            <Spinner></Spinner>
          </Our.Flex>
        </Our.Box>
      </Our.Container>
    );
  }

  return (
    <div className="App">
      <UserContext.Provider value={{ user }}>
        <ApolloProvider client={client}>
          <Router basename={window.location.pathname || ""}>
            <Switch>
              <Route exact path="/" component={Home}></Route>
              <Route exact path="/results" component={SearchResults}></Route>
              <PrivateRoute
                isAuthenticated={user}
                exact
                path="/my-listings"
                component={MyListings}
              ></PrivateRoute>
              <PrivateRoute
                isAuthenticated={user}
                path="/my-listings/:placeId"
                component={MyPlace}
              ></PrivateRoute>
              <PrivateRoute
                isAuthenticated={user}
                path="/my-account"
                component={MyAccount}
              ></PrivateRoute>
              <PrivateRoute
                isAuthenticated={user}
                path="/my-bookmarks"
                component={MyBookmarks}
              ></PrivateRoute>
              <Route path="/login" component={Login}></Route>
            </Switch>
          </Router>
        </ApolloProvider>
      </UserContext.Provider>
    </div>
  );
}

export default App;
