import React, { useState } from "react";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { auth, useSession } from "../services/firebase";
import { gql, useQuery, useMutation } from "@apollo/client";
import InputPlace from "../components/InputPlace";
import Spinner from "../components/Spinner";
import { useHistory } from "react-router";

const GET_USER = gql`
  query GetUser($id: ID!) {
    user(id: $id) {
      id
      places {
        id
        name
        address
        seeds {
          id
          name
          count
        }
      }
    }
  }
`;

const ADD_PLACE = gql`
  mutation AddPlace($input: AddPlaceInput) {
    addPlace(input: $input) {
      id
    }
  }
`;

const REMOVE_PLACE = gql`
  mutation RemovePlace($id: ID!) {
    removePlace(id: $id) {
      id
    }
  }
`;

export default function MyAccount() {
  const userSession = useSession();
  const history = useHistory();

  const [addPlaceModalIsOpen, setAddPlaceModalIsOpen] = useState(false);

  const { data: userData, loading: loadingUserData } = useQuery(GET_USER, {
    variables: {
      id: userSession.uid,
    },
  });

  const [addPlace] = useMutation(ADD_PLACE);
  const [removePlace] = useMutation(REMOVE_PLACE);

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="lg" margin-y="xl">
          <Our.Flex align-items="center" justify-content="between">
            <Our.Text weight="500" tag="h1">
              Places
            </Our.Text>
            <Our.Button
              type="transparent"
              onClick={() => setAddPlaceModalIsOpen(true)}
            >
              <i className="gg-math-plus"></i>
            </Our.Button>
          </Our.Flex>
        </Our.Box>
        <Our.Box margin-y="lg">
          {loadingUserData && (
            <Our.Flex justify-content="center" align-items="center">
              <Spinner></Spinner>
            </Our.Flex>
          )}
          {userData && !userData.user.places.length && (
            <Our.Box padding-x="lg">
              <Our.Text tag="h3" weight="400">
                Add a place to list your seeds
              </Our.Text>
            </Our.Box>
          )}
          {userData && (
            <table className="clickable">
              <tbody>
                {userData.user.places.map((place) => {
                  return (
                    <tr
                      onClick={() => history.push(`/my-listings/${place.id}`)}
                      key={place.id}
                    >
                      <td>
                        <Our.Text full weight="400" tag="h3">
                          {place.name}
                        </Our.Text>
                        <Our.Text full weight="400" tag="p">
                          {place.address}
                        </Our.Text>
                      </td>
                      <td className="align-right">
                        <Our.Box
                          radius="sm"
                          inline
                          padding-y="sm"
                          padding-x="md"
                          bg="ui-light"
                        >
                          <Our.Text no-wrap tag="small">
                            {place.seeds && place.seeds.length}
                          </Our.Text>
                        </Our.Box>
                        <Our.Button squared type="transparent">
                          <i className="gg-chevron-right"></i>
                        </Our.Button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}
        </Our.Box>
      </Our.Container>
      <Our.Modal
        title="Add Place"
        open={addPlaceModalIsOpen}
        onToggle={(e) => setAddPlaceModalIsOpen(e.target.open)}
      >
        <InputPlace
          resetOnSubmit
          onSubmit={(input) => {
            setAddPlaceModalIsOpen(false);
            addPlace({
              variables: { input: { ...input, userId: userSession.uid } },
              refetchQueries: [
                {
                  query: GET_USER,
                  variables: { id: userSession.uid },
                },
              ],
            });
          }}
        ></InputPlace>
      </Our.Modal>
    </>
  );
}
