import React, { useState, useCallback, useEffect } from "react";
import * as Our from "../components/Core";
import Header from "../components/Header";
import Spinner from "../components/Spinner";
import SeedCard from "../components/SeedCard";
import { getGeohashRange } from "../utils";
import enums from "../constants/enums";
import { useSession, auth } from "../services/firebase";
import { useHistory, useLocation } from "react-router";
import { useQuery, gql } from "@apollo/client";
import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";

mapboxgl.accessToken =
  "pk.eyJ1IjoibGVpZnJpa3NoZWltIiwiYSI6ImNrYThza2VibzAyd3cycXBqZjRpNjdlc2MifQ.ohHSpM7z_MU_NLEsJ1r2lQ";

function useRouteQuery() {
  return new URLSearchParams(useLocation().search);
}

const SEEDS = gql`
  query Seeds($filter: SeedFilters!) {
    seeds(filter: $filter) {
      id
      name
      place {
        lat
        lng
        address
      }
    }
  }
`;

export default function SearchResults() {
  const userSession = useSession();
  const query = useRouteQuery();
  const history = useHistory("");

  const [mapInstance, setMapInstance] = useState(null);

  const id = query.get("id");
  const name = query.get("name");
  const lat = query.get("lat");
  const lng = query.get("lng");
  const geoHashRange = getGeohashRange(Number(lat), Number(lng));

  const [showFilters, setShowFilters] = useState(false);
  const [selectedPlace, setSelectedPlace] = useState(undefined);

  const { data: seedData, loading: loadingSeeds } = useQuery(SEEDS, {
    variables: {
      filter: {
        AND: [
          {
            geohash: {
              gte: geoHashRange.lower,
            },
          },
          {
            geohash: {
              lte: geoHashRange.upper,
            },
          },
        ],
      },
    },
  });

  function onMapLoad() {
    //  this is map instance
    this.resize();
  }

  function onMapMove() {
    // this is map instance
    const mapLng = this.getCenter().lng.toFixed(4);
    const mapLat = this.getCenter().lat.toFixed(4);
    const mapZoom = this.getZoom().toFixed(2);
    history.push(`/results?&lat=${mapLat}&lng=${mapLng}`);
  }

  const mapRef = useCallback((node) => {
    if (node !== null) {
      const map = new mapboxgl.Map({
        container: node,
        style: "mapbox://styles/mapbox/streets-v11",
        center: [lng, lat],
        zoom: 10,
      });
      setMapInstance(map);
      map.on("load", onMapLoad);
      map.on("moveend", onMapMove);
    }
    return () => {
      mapInstance.off("load", onMapLoad);
      mapInstance.off("moveend", onMapMove);
    };
  }, []);

  useEffect(() => {
    if (seedData && mapInstance) {
      seedData.seeds.map((seed) => {
        const marker = new mapboxgl.Marker()
          .setLngLat([seed.place.lng, seed.place.lat])
          .addTo(mapInstance);
      });
    }
  }, [JSON.stringify(seedData), mapInstance]);

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>

      <Our.Container center size="full">
        <>
          <Our.Grid gap="sm">
            <Our.GridItem sm="12" md="4">
              <Our.Box padding-t="md" padding-x="lg">
                <Our.Box inline margin-r="sm">
                  <Our.Button rounded size="sm" type="secondary">
                    Change location
                    <i className="gg-chevron-down" slot="end"></i>
                  </Our.Button>
                </Our.Box>
                <Our.Box inline margin-r="sm">
                  <Our.Button
                    rounded
                    size="sm"
                    type="secondary"
                    onClick={() => setShowFilters(!showFilters)}
                  >
                    Filters
                    <i className="gg-chevron-down" slot="end"></i>
                  </Our.Button>
                </Our.Box>
                {showFilters && (
                  <Our.Box margin-y="lg">
                    <Filters></Filters>
                  </Our.Box>
                )}

                <Our.Box margin-y="lg">
                  {loadingSeeds ? (
                    <Our.Flex justify-content="center" align-items="center">
                      <Spinner></Spinner>
                    </Our.Flex>
                  ) : (
                    seedData &&
                    seedData.seeds &&
                    seedData.seeds.map((seed) => (
                      <SeedCard key={seed.id} seed={seed}></SeedCard>
                    ))
                  )}
                </Our.Box>
              </Our.Box>
            </Our.GridItem>
            <Our.GridItem sm="none" md="8">
              <div ref={mapRef}></div>
            </Our.GridItem>
          </Our.Grid>
        </>
      </Our.Container>
    </>
  );
}

function Filters() {
  return (
    <>
      <Our.Box inline margin-r="md">
        <Our.Label>Sun conditions</Our.Label>
        <Our.Select>
          {enums.sunConditions.map((option) => (
            <Our.Option key={option} value={option}></Our.Option>
          ))}
        </Our.Select>
      </Our.Box>
      <Our.Box inline margin-r="md">
        <Our.Label>Soil conditions</Our.Label>
        <Our.Select>
          {enums.soilConditions.map((option) => (
            <Our.Option key={option} value={option}></Our.Option>
          ))}
        </Our.Select>
      </Our.Box>
      <Our.Box inline margin-r="md">
        <Our.Label>Water requirement</Our.Label>
        <Our.Range
          labels="Low,Medium,High"
          sensitivity="0.7"
          value="0,4"
          max="4"
          min="0"
          step="1"
        ></Our.Range>
      </Our.Box>
      <Our.Box inline margin-r="md">
        <Our.Label>Temperature</Our.Label>
        <Our.Range
          labels="0,10,20,30,40"
          sensitivity="0.7"
          value="0,40"
          max="40"
          min="-0"
          step="5"
        ></Our.Range>
      </Our.Box>
      <Our.Box inline margin-r="md">
        <Our.Label>What are you looking for?</Our.Label>
        {enums.seedCategories.map((option) => (
          <Our.Checkbox key={option} size="sm" value={option}>
            {option}
          </Our.Checkbox>
        ))}
      </Our.Box>
    </>
  );
}
