import React, { useState } from "react";
import geohash from "ngeohash";
import * as Our from "./Core";
import MapSearchInput from "./MapSearchInput";

export default function InputPlace({
  initialState = {
    name: "",
    address: "",
    lat: 0,
    lng: 0,
    geohash: "",
    countryCode: "",
  },
  resetOnSubmit = false,
  isLoading = false,
  onSubmit = () => {},
  submitButtonText = "Save",
}) {
  const [isChanged, setIsChanged] = useState(false);
  const [input, setInput] = useState(initialState);

  function handleSetInput(newInput) {
    setIsChanged(true);
    setInput(newInput);
  }

  function handleOnSubmit() {
    const { __typename, id, ...data } = input;
    onSubmit(data);
    setIsChanged(false);
    if (resetOnSubmit) setInput(initialState);
  }

  return (
    <>
      <Our.Container size="xs" center>
        <Our.Box margin-y="md">
          <Our.Label>Name</Our.Label>
          <Our.Input
            value={input.name}
            onChange={(e) => handleSetInput({ ...input, name: e.target.value })}
            full
            placeholder="Bob's Farm"
          ></Our.Input>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Place</Our.Label>
          <MapSearchInput
            onSelect={(place) => {
              handleSetInput({
                ...input,
                address: place.address,
                lat: place.lat,
                lng: place.lng,
                geohash: geohash.encode(place.lat, place.lng),
              });
            }}
          ></MapSearchInput>
        </Our.Box>
        <Our.Box margin-y="xl">
          <Our.Button
            disabled={isLoading || !isChanged}
            full
            type="primary"
            onClick={() => handleOnSubmit()}
          >
            {submitButtonText}
          </Our.Button>
        </Our.Box>
      </Our.Container>
    </>
  );
}
