export default `
type Query {
  user(id: ID!): User!
  seed(id: ID!): Seed!
  seeds(filter: SeedFilters): [Seed!]!
  place(id: ID!): Place!
}

type Mutation {
  signIn(input: SignInInput!): User!
  sinUp(input: SignUpInput!): User!

  updateUser(id: ID!, patch: UpdateUserInput): User!

  addSeed(input: AddSeedInput!): Seed!
  updateSeed(id: ID!, patch: UpdateSeedInput!): Seed!
  removeSeed(id: ID!): RemoveSeedPayload!

  addPlace(input: AddPlaceInput!): Place!
  updatePlace(id: ID!, patch: UpdatePlaceInput!): Place!
  removePlace(id: ID!): RemovePlacePayload!

}

input SignInInput {
  email: String!
  password: String!
}

input SignUpInput {
  email: String!
  password: String!
}

type Place {
  id: ID!
  name: String!
  address: String
  lat: Float!
  lng: Float!
  geohash: String!
  countryCode: String!
  seeds: [Seed!]
  user: User!
}

input AddPlaceInput {
  name: String!
  address: String!
  lat: Float!
  lng: Float!
  geohash: String!
  countryCode: String!
  userId: ID!
}

input UpdatePlaceInput {
  name: String
  address: String
  lat: Float
  lng: Float
  geohash: String
  countryCode: String
  userId: ID
}

type RemovePlacePayload {
  id: ID!
}

type Seed {
  id: ID!
  name: String!
  count: Int!
  date: String!
  soilConditions: [SoilCondition]
  waterConditions: [WaterCondition]
  sunConditions: [SunCondition]
  temperatureRange: TemperatureRange
  isPublic: Boolean!
  user: User!
  place: Place
  category: SeedCategory
}

input AddSeedInput {
  name: String!
  count: Int!
  date: String!
  soilConditions: [SoilCondition]
  waterConditions: [WaterCondition]
  sunConditions: [SunCondition]
  temperatureRange: TemperatureRangeInput
  isPublic: Boolean!
  category: SeedCategory
  userId: ID!
  placeId: ID
}

input UpdateSeedInput {
  name: String
  count: Int
  date: String
  isPublic: Boolean
  soilConditions: [SoilCondition]
  waterConditions: [WaterCondition]
  sunConditions: [SunCondition]
  temperatureRange: TemperatureRangeInput
  category: SeedCategory
  userId: ID
  placeId: ID
}

type RemoveSeedPayload {
  id: ID!
}

input SeedFilters {
  AND: [SeedFilter]
  OR: [SeedFilter]
}

input SeedFilter {
  id: String
  count: IntFilter
  geohash: StringFilter
  countryCode: StringFilter
  name: StringFilter
  category: StringFilter
}

type User {
  id: ID!
  places: [Place!]
  seeds: [Seed!]
}

input UpdateUserInput {
  countryCode: String
}

input TemperatureRangeInput {
  start: Int!
  end: Int!
}

type TemperatureRange {
  start: Int!
  end: Int!
}

enum SoilCondition {
  CLAY
  SILTY
  SANDY
  CHALKY
  PEATY
  LOAMY
}

enum SunCondition {
  FULL_SUN
  SHADE
  LIGHT_SHADE
  PARTIAL_SHADE
  FULL_SHADE
  DENSE_SHADE
}

enum WaterCondition {
  LOW
  MEDIUM
  HIGH
}

enum SeedCategory {
  NUTS
  BEANS
  BEETS
  BROCCOLI_CAULIFLOUR
  CARROTS
  CORN
  COVER_CROPS
  CUCUMBERS
  TREE_FRUIT
  BERRIES
  GOURDS
  GRAINS
  GREENS
  HERBS_AND_SPICES
  MELONS
  ONIONS
  PEPPERS
  RADISH_AND_TURNIPS
  SQUASH
  TOMATOES
}

input IntFilter {
  gt: Int
  gte: Int
  lt: Int
  lte: Int
  eq: Int
  neq: Int
}

input StringFilter {
  eq: String
  neq: String
  in: [String]
  gt: String
  gte: String
  lt: String
  lte: String
}

schema {
  query: Query
  mutation: Mutation
}
`;
